/**
 * url 
 * @param url [url]
 * @param map []
 */
export function formatUrl (url, map) {
  map = map || {}
  return url.replace(/{(\w+)}/g, function (match, key) {
    let value = map[key]
    return typeof value !== 'undefined' ? encodeURIComponent(value + '') : match
  })
}

/**
 * 
 */
export function isArray (obj) {
  return Object.prototype.toString.call(obj) === '[object Array]'
}

/**
 * 
 */
export function isObject (obj) {
  return Object.prototype.toString.call(obj) === '[object Object]'
}

/**
 * 
 * @param target []
 * @param source []
 */
export function deepCopy (target, source) {
  for (let key in source) {
    let copy = source[key]
    if (target === copy) continue // to avoid endless loop, such as window.window === window
    if (isArray(copy)) {
      target[key] = deepCopy(target[key] || [], copy)
    } else if (isObject(copy)) {
      target[key] = deepCopy(target[key] || {}, copy)
    } else {
      target[key] = copy
    }
  }
  return target
}

/**
 * underscore  waitfunc 
 *
 * @param  {function} func        
 * @param  {number}   wait        
 * @param  {boolean}  immediate   ture
 * @return {function}             
 */
export function debounce (func, wait, immediate) {
  let timeout, args, context, timestamp, result

  let later = function () {
    // 
    let last = +new Date() - timestamp
    // 0
    if (last < wait && last >= 0) {
      // clearTimeout(timeout)
      console.log(wait - last)
      timeout = setTimeout(later, wait - last)
    } else {
      // 
      timeout = null
      if (!immediate) {
        result = func.apply(context, args)
        if (!timeout) context = args = null
      }
    }
  }

  return function () {
    context = this
    args = arguments
    // 
    timestamp = +new Date()
    // 
    let callNow = immediate && !timeout
    // 
    if (!timeout) timeout = setTimeout(later, wait)
    if (callNow) {
      //   apply 
      result = func.apply(context, args)
      context = args = null
    }

    return result
  }
}

/**
 * underscore func   / wait
 *
 * @param  {function}   func      
 * @param  {number}     wait      
 * @param  {object}     options   {leading: false}
 *                                {trailing: false}
 *                                
 * @return {function}             
 */
export function throttle (func, wait, options = {}) {
  let context, args, result
  let timeout = null
  let previous = 0
  // let later = function () {
  //   previous = options.leading === false ? 0 : +new Date()
  //   timeout = null
  //   result = func.apply(context, args)
  //   if (!timeout) context = args = null
  // }
  return function () {
    let now = +new Date()
    // !previoustrue{leading: false}
    // (now - previous) = 0
    // remaining = waitif
    if (!previous && options.leading === false) previous = now
    let remaining = wait - (now - previous)
    context = this
    args = arguments
    if (remaining <= 0 || remaining > wait) {
      // if (timeout) {
      //   clearTimeout(timeout)
      //   timeout = null
      // }
      previous = now
      result = func.apply(context, args)
      if (!timeout) context = args = null
    }
    // else if (!timeout && options.trailing !== false) {
    //   timeout = setTimeout(later, remaining)
    // }
    return result
  }
}



